@import url(https://fonts.googleapis.com/css2?family=Didact+Gothic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
* {
  padding: 0;
  margin: 0;
  font-family: "Didact Gothic", sans-serif;
  box-sizing: border-box;
}
div {
  scroll-behavior: smooth;
}

.nft-col h1 {
  color: white;
  font-size: 48px;
  margin-left: 5rem;
  padding: 6rem;
}

.nft-col {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  height: 400px;
}

.nfts {
  padding-right: 85px;
  height: 325px;
}

.eg {
  background-color: rgba(226, 74, 213, 0.904);
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-evenly;
  align-content: center;
  align-items: center;
  text-align: center;
  height: 500px;
}

.eg .real {
  font-size: 48px;
  margin-left: 5rem;
  padding: 6rem;
}

.cryptos {
  padding-left: 12rem;
}

:root {
  --first-color: #e36115;
  --text-color: white;
  --dark-color: rgb(21, 8, 7);
  --light-color: white;

  --body-font: "Poppins", sans-serif;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
}

a {
  text-decoration: none;
  color: white;
}

.card-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-left: 8rem;
}

.card {
  width: 280px;
  height: 345px;
  background-color: rgb(21, 8, 7);
  background-color: var(--dark-color);
  border-radius: 1rem;
  color: white;
  /* overflow-y: hidden; */
  -webkit-transform: scale(1);
  -webkit-transition: 0.5s ease-out;
  color: white;
}

/* .pricing__container-card:nth-child(2) {
    transform: scale(1.05);
    background: #242424;
  } */

.card-img {
  margin-top: -2rem;
  margin-left: 3.4rem;
  width: 175px;
  height: 200px;
  margin-right: 150px;
  padding-top: 2rem;
  padding-bottom: -2rem;
  -webkit-filter: drop-shadow(5px 10px 15px rgba(8, 9, 13, 0.4));
          filter: drop-shadow(5px 10px 15px rgba(8, 9, 13, 0.4));
}

.card-content {
  transform: translateY(4rem);
  text-align: center;
}

.title {
  font-size: 28px;
  color: rgba(226, 74, 213, 0.904);
  /* margin-bottom: 0.5rem; */
  margin-top: 0;
}

.type {
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 15px;
  margin-top: -20px;
}

.info {
  font-size: 0.875rem;
  font-size: var(--small-font-size);
  text-align: left;
  text-align: initial;
  margin-bottom: 30px;
  opacity: 0;
}

.btnn {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 1rem 1.5rem;
  background-color: rgba(226, 74, 213, 0.904);
  border-radius: 1rem;
  font-weight: 600;
  transition: 0.2s;
  opacity: 0;
}

.btnn:hover {
  box-shadow: 0 18px 40px -12px rgba(242, 162, 12, 0.35);
}

.card-img,
.card-content,
.title,
.type,
.info {
  transition: 0.5s;
}

.card:hover {
  transform: scale(1.3);
  transition: 0.5s ease-out;
  color: white;
}

.card:hover .card-img {
  transform: translate(-1.5rem, -3.5rem) rotate(-21deg);
}

.card:hover .card-content {
  transform: translateY(1.5rem);
}

.card:hover .info {
  transform: translateX(2.5rem);
}

.card:hover .title {
  font-size: 20px;
  transform: translateX(-4.8rem);
  margin-bottom: 0;
}

.card:hover .type {
  transform: translateX(-4.5rem);
}

.card:hover .info,
.card:hover .btnn {
  transition-delay: 0.2s;
  opacity: 1.2;
}

:root {
  font-size: 16px;
}

main {
  margin-left: 5rem;
  padding: 1rem;
}

.navbar {
  position: fixed;
  background-color: black;
  transition: width 0.2s ease;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.nav-item {
  width: 100%;
}

.nav-item:last-child {
  margin-top: auto;
}

.nav-link {
  display: flex;
  align-items: center;
  height: 5rem;
  color: rgb(188, 17, 204);
  text-decoration: none;
  -webkit-filter: grayscale(90%) opacity(0.7);
          filter: grayscale(90%) opacity(0.7);
  transition: 0.6s;
}

.nav-link:hover {
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
  background: #23232e;
  color: aliceblue;
}

.link-text {
  margin-left: 1rem;
  display: none;
  color: white;
}

.nav-link svg {
  max-width: 2rem;
  margin: 0 1.5rem;
}

.fa-primary {
  color: #ff7eee;
}

.fa-secondary {
  color: #df49a6;
}

.fa-primary,
.fa-secondary {
  transition: 0.6s;
}

.logo {
  font-weight: bold;
  text-transform: uppercase;
  padding-left: 0.5rem;
  margin-bottom: 1rem;
  text-align: center;
  color: #ececec;
  background: black;
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.logo svg {
  transform: rotate(0deg);
  transition: transform 0.6s;
}

.navbar:hover .logo svg {
  transform: rotate(-180deg);
}

@media only screen and (min-width: 600px) {
  .navbar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }

  .navbar:hover {
    width: 13rem;
  }
  .navbar:hover .link-text {
    display: block;
    transition: opacity 0.6s;
  }
}

@media only screen and (max-width: 600px) {
  .navbar {
    bottom: 0;
    width: 100vw;
    height: 5rem;
  }

  .logo {
    display: none;
  }
  .navbar-nav {
    flex-direction: row;
  }
  .nav-link {
    justify-content: center;
  }
  main {
    margin: 0;
  }
}

